export default {
  props: {
    bg: {
      type: String,
      default: ''
    },  
  
    title: {
      type: String,
      required: true
    },
    size: {
      type: String,
      default: 'medium'
    },   
    showImg: {
      type: Function,
      default: function() {
        return false
      }
    }
  },
  computed: {
    setSize: function() {
      switch (this.size) {
        case 'small':
          return 'small'
        case 'big':
          return 'big'
        default:
          return 'medium'
      }
    }
  }
}
